.header-nav {
  position: relative;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 10px 30px;
  background: rgba(255, 255, 255, 0.9);
  transition-duration: 0.3s;
  z-index: 2;
}
.nav-item {
  width: 13%;
}
.nav-item.current {
  background: rgb(240, 199, 142);
}
.nav-item.current:hover {
  background: rgb(240, 199, 142);
  opacity: 0.9;
}
.nav-item:hover {
  background: rgba(0, 0, 0, 0.1);
}
.nav-item a {
  text-decoration: none;
  text-align: center;
}
.nav-item h1 {
  padding-bottom: 5px;
  padding-top: 3px;
}
.nav-item img {
  width: 50px;
  height: 50px;
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 600px) {
  .header-nav {
    opacity: 0;
    visibility: hidden;
    transform: translateY(-100%);
    transition-duration: 0.3s;
    flex-direction: column;
    justify-content: space-between;
  }
  .nav-item {
    width: 100%;
    padding: 5px;
  }
  .header-nav.active {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
}